ves in and has continued to be one of my greatest supporters as I've taken my own design business full-time. He leads his projects well, with the utmost respect and professionalism. He is down to earth, approachable, and carries his expertise with grace. I would trust any of my precious clients with him without a moment of hesitation, and that says a lot. I could not give a higher recommendation. Any company would be blessed to have him!")]),_c('div',{staticClass:"subheading mb-3"},[_vm._v("Sara Brace")]),_c('p',[_vm._v("It's rare that you come across unprecedented talent like Cooper. As a Social Media Specialist at SocialSEO, I've had the opportunity to work with Cooper on several accounts and projects. With a creative AND analytical mindset, Cooper is a remarkable resource regarding topics on SEO and beyond, as he is undoubtedly well-rounded in his skillset. Cooper's evident eagerness to learn and excel shows that he's always ready to take on a challenge. As a dependable employee, he is always ready to help his teammates and superiors to bring clients success. I'm happy I had the chance to work alongside Cooper with joint accounts and hope we get to work together again in the future.")])])])])])])}]

